import React, { useEffect } from 'react';
import Navigation from './components/Navigation';
import PersonalInfo from './components/PersonalInfo';
import Skills from './components/Skills';
import Experience from './components/Experience';
import Projects from './components/Projects';
import Education from './components/Education';
import Biography from './components/Biography';
import Contact from './components/Contact';
import AnimatedSection from './components/AnimatedSection';
import ScrollProgressBar from './components/ScrollProgressBar';

function App() {
  useEffect(() => {
    //  CSS 
    document.documentElement.style.scrollBehavior = 'smooth';
    
    return () => {
      document.documentElement.style.scrollBehavior = 'auto';
    };
  }, []);

  return (
    <div className="App">
      <ScrollProgressBar />
      <Navigation />
      
      {/* Hero Section */}
      <AnimatedSection className="hero-section" animation="scaleIn">
        <div className="hero-background">
          <div className="hero-content">
            <div className="hero-avatar">
              <div className="avatar-circle">
                <span className="avatar-text"></span>
              </div>
              <div className="avatar-status">
                <div className="status-dot"></div>
                <span></span>
              </div>
            </div>
            <h1 className="hero-title">
              <span className="hero-greeting"></span>
              <span className="hero-name"></span>
            </h1>
            <div className="hero-roles">
              <span className="role-tag primary"></span>
              <span className="role-tag secondary">AR/VR </span>
              <span className="role-tag tertiary">Android </span>
            </div>
            <p className="hero-description">
               AR/VR  Android 
              
            </p>
            <div className="hero-actions">
              <button 
                className="btn-primary"
                onClick={() => document.getElementById('projects')?.scrollIntoView({ behavior: 'smooth' })}
              >
                
              </button>
              <button 
                className="btn-secondary"
                onClick={() => document.getElementById('contact')?.scrollIntoView({ behavior: 'smooth' })}
              >
                
              </button>
            </div>
          </div>
          <div className="hero-decoration">
            <div className="floating-element element-1"></div>
            <div className="floating-element element-2"></div>
            <div className="floating-element element-3"></div>
          </div>
        </div>
      </AnimatedSection>

      <div className="main-content">
        <div className="content-wrapper">
          <PersonalInfo />
          <Skills />
          <Experience />
          <Projects />
          <Education />
          <Biography />
        </div>
        <Contact />
      </div>
    </div>
  );
}

export default App;